[class*="vChat"] * {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

[class*="vChat"] {
	font-family: "Poppins", sans-serif;
}

//circle animation
.vc-bubble {
	&.circle-bubble {
		position: relative;
		z-index: 9999;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background-color: var(--color-primary);
		transition: 0.4s ease all;
		cursor: pointer;
		border: none;
		&:hover {
			background-color: var(--color-secondary);
		}
	}
	&[class*="circle-animation-"] {
		.open-icon {
			color: var(--color-light);
			font-size: 25px;
			transition: 0.4s ease all;
			left: 50%;
			top: 50%;
			position: absolute;
		}
		.close-icon {
			position: absolute;
			transition: 0.4s ease all;
			color: var(--color-light);
			font-size: 25px;
			opacity: 0;
			left: 50%;
			top: 50%;
		}

		&.circle-animation-1 {
			.open-icon {
				transform: translate(-50%, -50%);
			}
			.close-icon {
				transform: scale(0.7);
			}
		}
		&.circle-animation-2 {
			display: flex;
			align-items: center;
			justify-content: center;
			.open-icon {
				left: 8px !important;
				top: 2px !important;
				position: relative !important;
				transform: scale(1) rotate(360deg);
			}
			.close-icon {
				z-index: 2;
				transform: scale(0.7);
				position: relative;
				right: 12px;
				left: auto;
				top: auto;
			}
		}
		&.circle-animation-3 {
			.open-icon {
				transform: translate(-50%, -50%);
			}
			.close-icon {
				transform: translate(-50%, -50%);
			}
		}
		&.circle-animation-4 {
			overflow: hidden;
			.open-icon {
				transform: translate(-50%, -50%);
			}
			.close-icon {
				transform: translate(-40%, -150%);
			}
		}
	}
}

.vChat-show {
	.vc-bubble {
		&[class*="circle-animation-"] {
			.open-icon {
				opacity: 0;
			}
			.close-icon {
				opacity: 1;
			}
			&.circle-animation-1 {
				.open-icon {
					transform: scale(0.7);
				}
				.close-icon {
					transform: scale(1) translate(-50%, -50%);
				}
			}
			&.circle-animation-2 {
				.open-icon {
					transform: scale(0.7);
					bottom: 35px;
				}
				.close-icon {
					transform: scale(1) rotate(360deg);
				}
			}
			&.circle-animation-4 {
				.open-icon {
					transform: translate(-40%, -150%);
				}
				.close-icon {
					transform: translate(-50%, -50%);
				}
			}
		}
	}
}

.vChat {
	//   z-index: 999;
	position: fixed;
	bottom: 25px;
	right: 30px;
	@media screen and (max-width: 767px) {
		right: 15px;
		bottom: 15px;
	}
	&.vChat-left {
		left: 30px;
		right: auto;
		.vChat__popup {
			right: auto;
		}
		@media screen and (max-width: 767px) {
			right: 15px;
			bottom: 15px;
		}
	}

	&__send-message {
		padding: 10px 20px;
		border-radius: 20px;
		background-color: var(--color-primary);
		color: var(--color-light);
		transition: all 0.5s;
		text-decoration: none;
		width: 100%;
		font-size: 14px;
		margin-top: 20px;
		display: inline-block;
		text-align: center;
		position: relative;
		border: none;
		&:hover {
			background-color: var(--color-secondary);
			color: var(--color-light);
		}
		a {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
		}
		i {
			margin-right: 5px;
		}
	}
	&__popup {
		position: absolute;
		width: 350px;
		bottom: 100%;
		right: 0;
		margin-bottom: 20px;
		opacity: 0;
		visibility: hidden;
		transition: all 0.3s;
		box-shadow: var(--shadow);
		border-radius: 5px;
		background-color: var(--color-light);
		@media screen and (max-width: 767px) {
			width: 290px;
		}
		&.animation1 {
			transform: translate(20px);
		}
		&.animation2 {
			transform: translate(0, 50px);
		}
		&.animation3 {
			transform: translateY(100px) scale(0.7);
		}
		&.animation4 {
			transform: scale(0.7);
		}
		&.animation5 {
			transform: scale(0) rotate(720deg);
		}
		&.animation6 {
			transform: translate(30%) translateZ(600px) rotate(10deg);
		}
		&.animation7 {
			transform: translateY(100%) rotateX(90deg);
		}
		&.animation8 {
			transform: translateZ(100px) translateX(-30%) rotateY(90deg);
		}
		&.animation9 {
			transform: rotateY(-70deg);
		}
		&.animation10 {
			transform: rotateX(-70deg);
		}
		&.animation11 {
			transform: rotateX(-60deg);
			transform-origin: 50% 0;
		}
		&.animation12 {
			transform: scale(2);
		}
		&.animation13 {
			transform: translateY(-40%);
		}
		&--header {
			display: flex;
			gap: 10px;
			align-items: center;
			padding: 20px;
			background-color: var(--color-primary);
			border-top-left-radius: 5px;
			border-top-right-radius: 5px;
			@media screen and (max-width: 767px) {
				padding: 15px;
			}
			&.header-center {
				flex-direction: column;
				justify-content: center;
				text-align: center;
			}
			.image {
				width: 50px;
				height: 50px;
				position: relative;
				&::before {
					content: "";
					bottom: 0px;
					right: 0px;
					width: 12px;
					height: 12px;
					box-sizing: border-box;
					background-color: var(--color-active);
					position: absolute;
					z-index: 1;
					border-radius: 50%;
					border: 1px solid var(--color-primary);
				}
				img {
					border-radius: 50%;
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			.info {
				&__name {
					font-size: 20px;
					line-height: 1;
					margin-bottom: 5px;
					color: var(--color-light);
				}
				&__title {
					font-size: 14px;
					line-height: 1.5;
					color: var(--color-light);
				}
			}
		}
		&--content {
			padding: 20px;
			@media screen and (max-width: 767px) {
				padding: 15px;
			}
			.sms {
				display: grid;
				gap: 15px;
				grid-template-columns: 30px 1fr;
				&__user {
					width: 30px;
					height: 30px;
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 50%;
					}
				}
				&__text {
					padding: 10px;
					background-color: var(--color-message);
					border-radius: 0 5px 5px;
					position: relative;
					&::before {
						position: absolute;
						content: "";
						top: 0;
						left: -14px;
						border-bottom: 15px solid transparent;
						border-right: 15px solid var(--color-message);
					}
					p {
						font-size: 14px;
						line-height: 1.6;
					}
				}
			}
			.current-time {
				text-align: center;
				font-size: 14px;
				margin-bottom: 8px;
			}
		}
		&.avatar-inactive {
			.vChat__popup--header {
				.image {
					&::before {
						background-color: var(--color-inactive) !important;
					}
				}
			}
			.vChat__send-message {
				background-color: var(--color-offline);
				cursor: not-allowed;
			}
		}
	}
	&.vChat-show {
		.vChat__popup {
			opacity: 1;
			visibility: visible;
			&.animation1 {
				transform: translateX(0);
			}
			&.animation2 {
				transform: translate(0, 0);
			}
			&.animation3 {
				transform: translateY(0) scale(1);
			}
			&.animation4 {
				transform: scale(1);
			}
			&.animation5 {
				transform: scale(1) rotate(0deg);
			}
			&.animation6 {
				transform: translate(0%) translateZ(0) rotate(0deg);
			}
			&.animation7 {
				transform: translateY(0%) rotateX(0deg);
			}
			&.animation8 {
				transform: translateZ(0px) translateX(0%) rotateY(0deg);
			}
			&.animation9 {
				transform: rotateY(0deg);
			}
			&.animation10 {
				transform: rotateX(0deg);
			}
			&.animation11 {
				transform: rotateX(0deg);
			}
			&.animation12 {
				transform: scale(1);
			}
			&.animation13 {
				transform: translateY(0%);
			}
		}
	}
}

//////////////
/// Animation
@-webkit-keyframes slit {
	50% {
		-webkit-transform: translateZ(-250px) rotateY(89deg);
		opacity: 0.5;
		-webkit-animation-timing-function: ease-out;
	}
	100% {
		-webkit-transform: translateZ(0) rotateY(0deg);
		opacity: 1;
	}
}
@-moz-keyframes slit {
	50% {
		-moz-transform: translateZ(-250px) rotateY(89deg);
		opacity: 0.5;
		-moz-animation-timing-function: ease-out;
	}
	100% {
		-moz-transform: translateZ(0) rotateY(0deg);
		opacity: 1;
	}
}
@keyframes slit {
	50% {
		transform: translateZ(-250px) rotateY(89deg);
		opacity: 1;
		animation-timing-function: ease-in;
	}
	100% {
		transform: translateZ(0) rotateY(0deg);
		opacity: 1;
	}
}

[class*="vChat-button-"] {
	padding: 15px 30px;
	border: none;
	color: var(--color-light) !important;
	font-weight: 600;
	display: inline-flex;
	font-size: 16px;
	gap: 10px;
	align-items: center;
	transition: all 0.5s;
	cursor: pointer;
	text-decoration: none;
	border: 1px solid var(--color-primary);
	background-color: var(--color-light);
	i {
		font-size: 20px;
		transition: all 0.5s;
		margin-left: -5px;
		color: var(--color-primary);
	}
	&:hover {
		background-color: var(--color-secondary);
		color: var(--color-light);
		i {
			color: var(--color-light);
		}
	}
	&.vc-btn-rounded {
		border-radius: 30px;
	}
	&.vc-btn-lg {
		padding: 15px 35px;
		font-size: 18px;
		i {
			font-size: 25px;
		}
	}
	&.vc-btn-sm {
		padding: 10px 15px;
	}
	&.vc-btn-effect {
		&:hover {
			transform: translateY(-10px);
		}
	}
	&.vc-btn-bg {
		background-color: var(--color-primary);
		color: var(--color-light);
		border: none;
		i {
			color: var(--color-light);
		}
		&:hover {
			background-color: var(--color-secondary);
		}
	}
	&.vChat-button-3 {
		background-color: var(--color-light);
		color: var(--color-dark);
		border-radius: 5px;
		padding: 5px 10px;
		i {
			width: 45px;
			height: 45px;
			border-radius: 50%;
			padding: 0;
			border: none;
			font-size: 20px;
			background: var(--color-primary);
			color: var(--color-light);
			display: flex;
			align-items: center;
			justify-content: center;
			transition: all 0.5s;
		}
		&:hover {
			background-color: var(--color-secondary);
			color: var(--color-light);
			i {
				background-color: var(--color-light);
				color: var(--color-primary);
			}
		}
		&.vc-btn-lg {
			padding: 10px 15px;
			font-size: 18px;
			i {
				width: 50px;
				height: 50px;
			}
		}
		&.vc-btn-sm {
			font-size: 14px;
			i {
				width: 40px;
				height: 40px;
			}
		}
		&.vc-btn-rounded {
			border-radius: 45px;
		}
		&.vc-btn-bg {
			background-color: var(--color-primary);
			color: var(--color-light);
			i {
				background-color: var(--color-light);
				color: var(--color-primary);
			}
			&:hover {
				background-color: var(--color-secondary);
			}
		}
	}
	&.avatar-active,
	&.avatar-inactive {
		line-height: 1;
		padding: 7px 18px 7px 10px;
		border-radius: 5px;
		position: relative;
		a {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
		}
		img {
			width: 55px;
			height: 55px;
			border-radius: 50%;
			object-fit: cover;
			padding: 0;
			border: 1px solid var(--color-light) !important;
		}
		&.vc-btn-lg {
			padding: 10px 13px;
			.title {
				font-size: 17px;
			}
			img {
				width: 60px;
				height: 60px;
			}
		}
		&.vc-btn-sm {
			.title {
				font-size: 13px;
			}
			img {
				width: 50px;
				height: 50px;
			}
		}
		.info-wrapper {
			text-align: left;
		}
		.info {
			font-size: 10px;
			margin-bottom: 7px;
			line-height: 1;
		}
		.title {
			margin-bottom: 7px;
			font-weight: 700;
			line-height: 1;
			font-size: 16px;
		}
		.online {
			display: inline-block;
			padding: 5px;
			background-color: var(--color-active);
			font-size: 10px;
			border-radius: 5px;
			line-height: 1;
		}
		.offline {
			display: inline-block;
			padding: 5px;
			background-color: var(--color-active);
			font-size: 10px;
			border-radius: 5px;
			line-height: 1;
		}
		&.vc-btn-rounded {
			border-radius: 50px;
		}
	}
	&.avatar-active {
		.offline {
			display: none;
		}
	}
	&.avatar-inactive {
		border: none;
	}
	&.avatar-inactive,
	&.avatar-inactive:hover {
		background-color: var(--color-offline) !important;
		color: var(--color-light) !important;
		cursor: not-allowed;
		border-color: var(--color-dark) !important;
		.online {
			display: none;
		}
		.offline {
			background-color: var(--color-inactive) !important;
		}
	}
}

