						cursor: not-allowed;
					}
				}
				&.avatar-active {
					.user__info--online {
						display: table;
					}
				}
				&.avatar-inactive {
					border-left: 2px solid transparent;
					cursor: not-allowed;
					&:hover {
						border-left: 2px solid var(--color-inactive) !important;
					}
					.user__info--offline {
						display: table;
					}
				}
			}
		}
	}
	&.vChat-show {
		.vChat-multi__popup {
			opacity: 1;
			visibility: visible;
			&.animation1 {
				transform: translateX(0);
			}
			&.animation2 {
				transform: translate(0, 0);
			}
			&.animation3 {
				transform: translateY(0) scale(1);
			}
			&.animation4 {
				transform: scale(1);
			}
			&.animation5 {
				transform: scale(1) rotate(0deg);
			}
			&.animation6 {
				transform: translate(0%) translateZ(0) rotate(0deg);
			}
			&.animation7 {
				transform: translateY(0%) rotateX(0deg);
			}
			&.animation8 {
				transform: translateZ(0px) translateX(0%) rotateY(0deg);
			}
			&.animation9 {
				transform: rotateY(0deg);
			}
			&.animation10 {
				transform: rotateX(0deg);
			}
			&.animation11 {
				transform: rotateX(0deg);
			}
			&.animation12 {
				transform: scale(1);
			}
			&.animation13 {
				transform: translateY(0%);
			}
		}
	}
	&.vChat-grid {
		.vChat-multi__popup--content {
			padding: 10px;
			.users {
				display: grid;
				grid-template-columns: 1fr 1fr;
				gap: 8px;
				.user {
					&.avatar-inactive,
					&.avatar-active {
						.user__info--offline,
						.user__info--online {
							display: none;
						}
					}
					&__image {
						position: relative;
						&::before {
							content: "";
							bottom: 1px;
							right: 2px;
							width: 10px;
							height: 10px;
							box-sizing: border-box;
							background-color: var(--color-active) !important;
							position: absolute;
							z-index: 1;
							border-radius: 50%;
						}
					}
					&.avatar-inactive {
						.user__image {
							&::before {
								background-color: var(
									--color-inactive
								) !important;
							}
						}
					}
					margin-bottom: 0;
					display: flex;
					flex-direction: column;
				}
			}
		}
		.user {
			display: inherit;
			text-align: center;
		}
	}
}

.vc-scroll {
	max-height: 295px;
	overflow-y: scroll;
	scrollbar-width: thin;
	&::-webkit-scrollbar {
		width: 0.5em;
		height: 0.5em;
	}
	&::-webkit-scrollbar-track {
		background-color: var(--color-message);
		margin-block: 0.5em;
	}
	&::-webkit-scrollbar-thumb {
		background: var(--color-primary);
	}
	&::-webkit-scrollbar-thumb:hover {
		opacity: 0.2;
	}
}

.night-mode {
