/*
* SMALL BUBBLES WITH ANIMATIONS
*/
.bubble {
	&:focus,
	&:active {
		background-color: var(--color-light);
		border: 1px solid var(--color-primary);
		padding: 5px 10px;
	}
	display: flex;
	background-color: var(--color-light);
	color: var(--color-primary);
	border: 1px solid var(--color-primary);
	padding: 5px 10px;
	border-radius: 5px;
	font-weight: 600;
	display: inline-flex;
	font-size: 16px;
	gap: 10px;
	align-items: center;
	transition: all 0.5s;
	cursor: pointer;
	text-decoration: none;
	&:hover {
		background-color: var(--color-primary);
		color: var(--color-light);
		.bubble__icon {
			background-color: var(--color-light);
			color: var(--color-primary);
		}
	}
	&.bubble-transparent {
		gap: 0;
		.bubble__icon {
			background-color: transparent !important;
			width: 30px;
			color: var(--color-light);
			.bubble__icon--open,
			.bubble__icon--close {
				color: var(--color-primary);
			}
		}
		&:hover {
			.bubble__icon {
				.bubble__icon--open,
				.bubble__icon--close {
					color: var(--color-light);
				}
			}
		}
		&.vc-btn-bg {
			.bubble__icon--open,
			.bubble__icon--close {
				color: var(--color-light);
			}
		}
	}
	&__icon {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: var(--color-primary);
		color: var(--color-light);
		width: 35px;
		height: 35px;
		border-radius: 50%;
		position: relative;
		z-index: 9999;
		transition: all 0.5s;
		border: none;
		margin-left: -5px;
		&--open {
			left: 8px;
			top: 2px;
			position: relative;
			font-size: 20px;
			transition: 0.4s ease all;
		}
		&--close {
			z-index: 2;
			position: relative;
			right: 12px;
			left: auto;
			top: auto;
			transition: 0.4s ease all;
			font-size: 20px;
			opacity: 0;
		}
		&.bubble-animation1 {
			.bubble__icon--open {
				left: 50%;
				top: 50%;
				transform: translate(-61%, -74%);
			}
			.bubble__icon--close {
				left: 53%;
				top: 50%;
				transform: scale(0.7);
			}
		}
		&.bubble-animation2 {
			.bubble__icon--open {
				left: 6px;
				transform: scale(1) rotate(360deg);
			}
			.bubble__icon--close {
				right: 10px;
				transform: scale(0.7);
			}
		}
		&.bubble-animation3 {
			.bubble__icon--open {
				left: 6px;
			}
			.bubble__icon--close {
				right: 10px;
			}
		}
		&.bubble-animation4 {
			.bubble__icon--open {
				left: 16px;
				top: 15px;
				transform: translate(-48%, -58%);
			}
			.bubble__icon--close {
				left: -6px;
				top: 15px;
				transform: translate(-40%, -150%);
			}
		}
	}
	&.vc-btn-bg {
		background-color: var(--color-primary);
		color: var(--color-light);
		.bubble__icon {
			background-color: var(--color-light);
			color: var(--color-primary);
		}
		&:hover {
			background-color: var(--color-secondary);
		}
	}
	&.vc-btn-lg {
		font-size: 18px;
		.bubble__icon {
			width: 55px;
			height: 55px;
		}
	}
	&.vc-btn-sm {
		font-size: 14px;
		.bubble__icon {
			width: 40px;
			height: 40px;
		}
	}
	&.vc-btn-rounded {
		border-radius: 45px;
	}
}

.vChat-show {
	.bubble {
		&__icon {
			&--open {
				bottom: 35px;
				opacity: 0;
			}
			&--close {
				opacity: 1;
			}
			&.bubble-animation1 {
				.bubble__icon--open {
					transform: scale(0.7);
				}
				.bubble__icon--close {
					transform: scale(1) translate(-231%, -73%);
				}
			}
			&.bubble-animation2 {
				.bubble__icon--open {
					transform: scale(0.7);
				}
				.bubble__icon--close {
					transform: scale(1) rotate(360deg);
				}
			}
			&.bubble-animation4 {
				.bubble__icon--open {
					transform: translate(-40%, -150%);
				}
				.bubble__icon--close {
					transform: translate(-35%, -67%);
				}
			}
		}
	}
}
